/*====
====
====
S-Mobile
====
====
====*/

/*--
--S-Nav info
--
*/

.wp-block-create-block-sidebarnav{
	position: fixed;
	pointer-events: none;
	inset: 0;
	z-index: 10000;
}

.wp-block-create-block-sidebarnav #navInfo{
	position: fixed;
	inset: 0 0 auto 0;
	padding: 1rem;
}

.wp-block-create-block-sidebarnav #navInfo #navName{
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	position: relative;
	z-index: 100;
}

.wp-block-create-block-sidebarnav #navInfo #navName h1{
	font-size: 1.75rem;
	margin: 0px;
}

.wp-block-create-block-sidebarnav #navInfo svg{
	height: 1.5rem;
}

.wp-block-create-block-sidebarnav #navInfo::before{
	content:'';
	display: block;
	z-index: 50;
	position: absolute;
	inset: 0% 0% 100% 0%;
	transition: inset 0.25s ease-in;
	background-color: rgba(20, 21, 20, 1);
}
.SCRL .wp-block-create-block-sidebarnav #navInfo::before{
	inset: 0% 0% 0% 0%;
}

.wp-block-create-block-sidebarnav #navTagline .lastName{
	display: none;
}

.wp-block-create-block-sidebarnav #navTagline{
	transition: 0.25s ease-in-out;
	height: 3.5em;
}

.SCRL .wp-block-create-block-sidebarnav #navTagline{
	transform: translateY(-200%);
	height: 0em;
}

.wp-block-create-block-sidebarnav #navTagline h2{
	font-size: 1rem;
	margin: 0px;
	transition: transform 0.25s ease-in-out;
	transform: translateY(0%);
	font-family:'IBM plex mono';
}
.SCRL .wp-block-create-block-sidebarnav #navTagline h2{
	transform: translateY(-120%);
}

.wp-block-create-block-sidebarnav #navTagline h2:nth-child(3){
	text-align: right;
}


/*--
--S-NaV Menu
--
*/

.wp-block-create-block-sidebarnav .navLinks{
	position: fixed;
	inset: auto 0 0 0;
	pointer-events: all;
}

.wp-block-create-block-sidebarnav .wp-block-navigation__responsive-container-open{
	background-color: black;
	margin:auto 1rem 1rem auto;
	transition: margin 0.4s cubic-bezier(.8,.11,.24,.94);
}
.wp-block-create-block-sidebarnav nav:has(.has-modal-open) .wp-block-navigation__responsive-container-open{
	margin: auto 1rem 5rem auto;
	transition: margin 0.4s cubic-bezier(.07,.14,0,.98);
}

.wp-block-create-block-sidebarnav button svg{
	transition:transform 0.4s ease-in-out;
	transform-origin:center;
	transform:rotate(0deg);
	width: 2.25rem;
	height: 2.25rem;
}

.wp-block-create-block-sidebarnav nav:has(.has-modal-open) button svg{
	transition:transform 0.4s ease-in-out;
	transform-origin:center;
	transform:rotate(90deg);
	width: 2.25rem;
	height: 2.25rem;
}

.wp-block-create-block-sidebarnav .wp-block-navigation__responsive-container{
	top: auto !important;
	animation:none!important;
	transform-origin: center;
	transform: translateY(100%);
}

.wp-block-create-block-sidebarnav .wp-block-navigation__responsive-container.has-modal-open{
	animation:none!important;
	top: auto !important;
	transition: transform 0.5s cubic-bezier(.51,1.02,.08,.97) !important;
	transform: translateY(0%);
}

.wp-block-create-block-sidebarnav .wp-block-navigation-item__content{
	font-family:'IBM Plex Mono';
	font-weight: 700;
	font-size:1.5rem;
	text-transform: lowercase;
}




/*====
====
====
S-Desktop 768
====
====
====*/
@media (min-width:768px){




.wp-block-create-block-sidebarnav{
	position: fixed;
	pointer-events: none;
	inset: 0 0 auto 0;
	z-index: 10000;
	display: flex;
	justify-content: space-between;
	align-items:stretch;
	margin: 0 !important;
}

.wp-block-create-block-sidebarnav::before{
	content:'';
	display: block;
	position: absolute;
	inset: 0 0 100% 0;
	transition:inset 0.25s ease-in-out;
	background-color: var(--wp--preset--color--black);
}
.SCRL .wp-block-create-block-sidebarnav::before{
	inset: 0 0 0 0;
}

/*--
--S-Nav info
--
*/

.wp-block-create-block-sidebarnav #navInfo{
	position: relative;
	inset: auto;
	pointer-events: all;
	width: auto;
}
.wp-block-create-block-sidebarnav #navInfo::before{
	display: none;
}

.wp-block-create-block-sidebarnav #navInfo #navName{
	justify-content: flex-end;
	gap:0.5rem;

}

.wp-block-create-block-sidebarnav #navInfo #logoBox{
	--logoSize:3em;
	transition: width 0.25s ease-in-out, height 0.25s ease-in-out;
	height: var(--logoSize);
	width: 3evar(--logoSize);
	flex-shrink: 0;
}
.SCRL .wp-block-create-block-sidebarnav #navInfo #logoBox{
	--logoSize:2em
}


.wp-block-create-block-sidebarnav #navInfo h1{
	font-size: 3rem !important;
	transition: font-size 0.25s ease-in-out;
}
.SCRL .wp-block-create-block-sidebarnav #navInfo h1{
	font-size: 2rem !important;
}
.wp-block-create-block-sidebarnav #navInfo #navName .lastName{
	width:0%;
	overflow: hidden;
	transition:width 0.5s ease-in-out, font-size 0.25s ease-in-out;
}


.SCRL .wp-block-create-block-sidebarnav #navInfo #navName .lastName{
	width:100%;
}

.wp-block-create-block-sidebarnav #navInfo #navTagline{
	position: absolute;
	left:100%;
	top: calc(100% - 0.5rem);
	padding: 1rem;
	width: 100%;
	transform-origin:top left;
	transform:rotate(90deg);
	padding-left:0;
	display: grid;
	transition: 0.35s ease-in-out;
	grid-template-columns:auto auto;
	grid-template-rows: 1fr 1fr;
}
.SCRL .wp-block-create-block-sidebarnav #navInfo #navTagline{
	height: auto !important;
	transform:rotate(90deg) translateY(-100%);
}
.wp-block-create-block-sidebarnav #navInfo #navTagline h2{
	white-space: nowrap;
	grid-column-start: 2;
	text-align: left;
}
.SCRL .wp-block-create-block-sidebarnav #navInfo #navTagline h2{
	transform: none;
}
.wp-block-create-block-sidebarnav #navInfo #navTagline .lastName{
	display: block;
	margin: 0;
	font-size: 3rem;
	grid-row: span 2;
	line-height:1;
}



/*--
--S-NaV Menu
--
*/
.wp-block-create-block-sidebarnav .navLinks{
	position:static;
	inset: auto;
	pointer-events: all;
	display: flex;
	align-items: center;

}

.wp-block-create-block-sidebarnav .wp-block-navigation__responsive-container{
	transform: none;
	padding: 1rem;
}

.wp-block-create-block-sidebarnav .wp-block-navigation-item__content{
	font-family:'IBM Plex Mono';
	font-weight: 700;
	font-size:1rem;
	text-transform: lowercase;
}

.wp-block-create-block-sidebarnav .wp-block-navigation-item__content:hover{
	color:var(--wp--preset--color--red);
	font-style: italic;
}

}